import t from '../../common/time.js';
export default {
	props:{
		userinfo:Object
	},
	computed:{
		getRegTime(){
			return t.gettime.sumAge(this.userinfo.regtime);
		},
		getXingZuo(){
			return t.gettime.getHoroscope(this.userinfo.birthday);
		}
	}
}
